import { useEffect, useState } from "react";
import totp from "totp-generator";

import "./App.css";

function App() {
  const [key, setKey] = useState("");
  const [code, setCode] = useState("");

  useEffect(() => {
    try {
      const secret = key.replaceAll(" ", "").toLocaleUpperCase();
      setCode(totp(secret));
    } catch (error) {
      console.log(error);
      setCode("");
    }
  }, [key]);

  return (
    <div className="container-fluid">
      <div className="row">
        <div className="col">
          <div className="form-group">
            <label for="key"></label>
            <textarea
              id="key"
              className="form-control"
              value={key}
              onChange={(e) => setKey(e.target.value)}
            ></textarea>
          </div>
        </div>
        <div className="col">
          <div className="form-group">
            <label for="code"></label>
            <textarea
              id="code"
              className="form-control"
              value={code}
              onChange={(e) => setCode(e.target.value)}
            ></textarea>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
